// keyframes 
nav {
   background-color: greenyellow;
   position:fixed;
   bottom:15px;
   left:15px;
   width: 30px;
   height: 30px;
   overflow:hidden;
   transition:height 0.5s, width 0.5s, left 0.5s, bottom 0.5s;
   ul.lis {
       .burger__li {
           opacity: 0;
       }
   }
   
   }

nav.is-open {
    background-color:brown;
    position:fixed;
    bottom: 0;
    left:0;
    z-index: 1;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    ul.lis{
        position:fixed;
        display:flex;
        flex-direction:column;
        justify-content:center;
        align-items:center;
        height: 100vh;
        width: 100vw;
      
      .burger__li {
          
          opacity:1;
        font-size: 40px;
        margin: -100px;
        
      }
      .burger__li:not(:last-child) {
        margin-bottom:0;
}
    }
}
 .header {
   background-color:$primarycolor;
   display:flex;
   justify-content: space-around;
   align-items: baseline;

}
