// Fans 
//Mobile
.fans{
 background-color: $secondarycolor;
 height: 40vh;
 color: $primarycolor;
 text-align: center;
 padding-top: 16px;
 font-size: 24px;
 // margin-bottom: 16px;
 .fans__img{
   display: grid;
   grid-template-columns: 1fr 1fr;
   grid-gap: 15px;
   grid-template-rows: 1fr 1fr;
   padding-top: 32px;
 }
 .button__fans{
   background-color: $primarycolor;
     border: none;
     color: #FFFFFF;
     height: 5vh;
     width: 48vw;
     font-size: 10px;
     border-radius: 20.5px;
     margin-top: 50px;
     display: block;
     margin: auto;
     box-shadow: 4px 4px 0px $thirdcolor;
 }
