.star1{
 height: 17.85vh;
 width: auto;
 grid-column: 1 / 2;
 grid-row: 1 / 2;
 align-self: center;
 justify-self: center;
}

.star2{
 height: 17.85vh;
 width: auto;
 grid-column: 2 / 2;
 grid-row: minmax (100px, auto);
 align-self: center;
 justify-self: center;
}

.star3{
 height: 17.85vh;
 width: auto;
 grid-column: 1 / 2;
 grid-row: 2 / 2;
 align-self: center;
 justify-self: center;
}

.star4{
 height: 17.85vh;
 width: auto;
 grid-column: 2 / 2;
 grid-row: 2 / 2;
 align-self: center;
 justify-self: center;
}

